@use 'base/variables' as v;
@use 'base/mixins' as m;

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
}
.search-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start; 
  padding: 10px;
}
.square-container {
  background-color: #fff;
  border: 2px solid #ccc;
  width: 700px;
  height: 400px;
  margin-left: 50px;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 10px;
  overflow: auto;
}
.scrollable-content {
  overflow: auto;
}
.input-container {
  display: flex;
  align-items: center;
  justify-content: space-between; 
  margin: 5px;
}
.input-container label {
  text-align: left; 
}
.input-container input {
  border: 2px solid #ccc;
  border-radius: 10px;
  padding: 10px;
  width: 350px; 
  text-align: left; 
}
.input-container input::placeholder {
  text-align: left; 
}
.search-header {
  text-align: left; 
}
.search-button {
  width: 100px;
  height: 100px;
  border: none; 
  border-radius: 20px; 
  background-color: v.$accent;
  color: #000000;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin: 0 auto;
}

.search-button:hover {
  background-color: #630707;
}

.result-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.result-list li {
  margin-bottom: 25px;
}


.line {
  border-bottom: 1px solid black;  
  text-align: center;
}

table.line{
  margin-bottom: 20px;
}

.link-btn{
  background-color: v.$accent; 
  color: v.$black;
  padding: 15px 25px; 
  border: none; 
  border-radius: 5px; 
  cursor: pointer;
  font-size: 16px; 
  text-align: center;
}

.link-btn:hover {
  background-color: #630707;
}

.link-btn2{
  background-color: #169c16;
  color: v.$black;
  padding: 15px 25px; 
  border: none; 
  border-radius: 5px; 
  cursor: pointer;
  font-size: 16px; 
  text-align: center;
}

.link-btn2:hover {
  background-color: #0c5c24;
}

.drop-down {
  display: flex;
  align-items: center;
  justify-content: space-between; 
  margin: 5px;
  margin-top:5px;
}

.drop-down label {
  text-align: left; 
}

.drop-down select {
  border: 2px solid #ccc;
  border-radius: 10px;
  padding: 10px;
  width: 350px; 
  text-align: left;
  background: url('path-to-dropdown-arrow-icon.png') no-repeat right center / 20px;
  cursor: pointer;
}

.drop-down select option {
  text-align: left;
}

.drop-down select option[disabled]:first-child {
  color: #999;
}

.description-container {
  display: flex;
  align-items: center;
  justify-content: space-between; 
}
.description-container label {
  text-align: left; 
}

.description-container textarea {
  border: 2px solid #ccc;;
  padding: 10px;
  width: 350px;
  height: 250px;
  overflow: auto; 
  resize: vertical; 
  user-select: text;
  border-radius: 10px; 
  word-wrap: break-word;
  margin-bottom: 3rem;
}

.description-container input::placeholder {
  text-align: left; 
}

.square-container-list {
  background-color: #fff;
  border: 2px solid #ccc;
  width: 1000px;
  height: 550px;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 10px;
  overflow: auto;
  margin-top: 20px;
}

.center{
  text-align: center;
  margin-top: 20px;
  margin-bottom: 20px;
}

.align-right{
  text-align: right;
  margin-top: 20px;
  margin-right: 30px;
}

.extra-margin{
  margin-right: 15px;
}

.submit-btn{
  width: 250px;
  height: 55px;
  border: none; 
  border-radius: 21px; 
  background-color: v.$accent;
  color: #000000;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin: 0 auto;
}

.submit-btn:hover {
  background-color: #630707;
}

.extra-margin2{
  margin-top: 15px;
  margin-bottom: 15px;
  text-align: center;
}
